// pages/dashboard/favorites.js
import { useEffect, useState } from "react";
import api from "../../utils/api";
import Link from "next/link";
import { useRouter } from "next/router";

export default function Favorites() {
  const router = useRouter();
  const [favorites, setFavorites] = useState([]);

  useEffect(() => {
    loadFavorites();
  }, []);

  const loadFavorites = async () => {
    try {
      const res = await api.get("/favorites");
      setFavorites(res.data);
    } catch (err) {
      router.push("/login");
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 p-5">
      <h2 className="text-center text-3xl font-bold mb-6">Favorites</h2>

      {favorites.length === 0 ? (
        <p className="text-center text-gray-500">No favorite posts yet.</p>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-5 max-w-4xl mx-auto">
          {favorites.map(fav => (
            <div key={fav._id} className="bg-white p-4 rounded-xl shadow-md">
              {fav.images?.length > 0 && (
                <img
                  src={`http://localhost:5000/uploads/${fav.images[0]}`}
                  className="w-full h-48 object-cover rounded-lg mb-3"
                />
              )}

              <h3 className="text-lg font-bold">{fav.title}</h3>
              <p className="text-gray-600">{fav.price} BDT</p>

              <Link
                href={`/post/${fav._id}`}
                className="text-blue-600 hover:underline block mt-2"
              >
                View Post â†’
              </Link>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
